import { ROOT } from "../src/function/constants";
import { useAmp } from "next/amp";

function Ernie(props) {
  const isAmp = useAmp();

  return (
    <div>
      {isAmp}
      <h3>My AMP About Page!</h3>
      {isAmp ? (
        <amp-img width="300" height="300" src="./images/love.jpg" alt="a cool image" layout="responsive" />
      ) : (
        <img src={`${ROOT}/images/love.jpg`} alt="love.jpg" style={{ maxWidth: "500px" }} />
      )}
    </div>
  );
}

export const config = { amp: "hybrid" };
export default Ernie;
